.dark-theme {
    color: white;
    background-color: #221e32;
}

.theme-btn {
    position: absolute;
    bottom: 15px;
    right: 15px;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
    fill: rgb(236, 233, 21);
    // background-color: rgba(118, 83, 154, 0.34);
    border-radius: 50%;
    padding: 0;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    z-index: 50;
    cursor: pointer;
}

.theme-light {
    fill: #fff;
    padding-top: 5px;
}

.is-hidden {
    display: none;
}

.dark{
    background-color: #221e32;
}
